import React from 'react';

function CatalogCard(props) {
    return (
        <div className="catalog_card">
          <div className="catalog_img_wrapper">
            <img className="catalog_card_img" src={props.descr.img} alt="Пример"/>
          </div>
          <div className="catalog_card_info">
            <div className="catalog_card_filter"></div>
            <span className="catalog_card_text">{props.descr.description}</span>
            <span className="catalog_card_price">{props.descr.price}</span>
          </div>
        </div>
    );
  }
  
  export default CatalogCard;